/**
 * Upload ·∫£nh s·∫£n ph·∫©m l√™n PKGX
 */
export async function uploadProductImage(
  imageFile: File,
  options?: {
    filenameSlug?: string;
    goodsId?: number;
  }
): Promise<ApiResponse<PkgxImageUploadResponse>> {
  const { apiUrl, apiKey, enabled } = getApiConfig();

  if (!enabled) {
    return { success: false, error: 'T√≠ch h·ª£p PKGX ch∆∞a ƒë∆∞·ª£c b·∫≠t' };
  }

  if (!apiKey) {
    return { success: false, error: 'Ch∆∞a c·∫•u h√¨nh API Key' };
  }

  const url = `${apiUrl}?action=upload_product_image`;
  const formData = new FormData();
  formData.append('image_file', imageFile);

  if (options?.filenameSlug) {
    formData.append('filename_slug', options.filenameSlug);
  }

  if (options?.goodsId) {
    formData.append('goods_id', options.goodsId.toString());
  }

  try {
    const response = await fetch(url, {
      method: 'POST',
      headers: { 'X-API-KEY': apiKey },
      body: formData,
    });

    const data = await response.json();

    if (data.error) {
      return { success: false, error: data.message };
    }

    return { success: true, data };
  } catch (error) {
    return {
      success: false,
      error: error instanceof Error ? error.message : 'Kh√¥ng th·ªÉ upload ·∫£nh',
    };
  }
}

/**
 * Upload ·∫£nh t·ª´ URL (server-to-server, tr√°nh CORS)
 * Server PKGX s·∫Ω t·ª± download ·∫£nh t·ª´ URL r·ªìi x·ª≠ l√Ω
 */
export async function uploadImageFromUrl(
  imageUrl: string,
  options?: {
    filenameSlug?: string;
    goodsId?: number;
  }
): Promise<ApiResponse<PkgxImageUploadResponse>> {
  const { apiUrl, apiKey, enabled } = getApiConfig();

  if (!enabled) {
    return { success: false, error: 'T√≠ch h·ª£p PKGX ch∆∞a ƒë∆∞·ª£c b·∫≠t' };
  }

  if (!apiKey) {
    return { success: false, error: 'Ch∆∞a c·∫•u h√¨nh API Key' };
  }

  const url = `${apiUrl}?action=upload_image_from_url`;
  
  const payload: Record<string, unknown> = {
    image_url: imageUrl,
  };

  if (options?.filenameSlug) {
    payload.filename_slug = options.filenameSlug;
  }

  if (options?.goodsId) {
    payload.goods_id = options.goodsId;
  }

  try {
    const response = await fetch(url, {
      method: 'POST',
      headers: { 
        'X-API-KEY': apiKey,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(payload),
    });

    const data = await response.json();

    if (data.error) {
      return { success: false, error: data.message };
    }

    return { success: true, data };
  } catch (error) {
    return {
      success: false,
      error: error instanceof Error ? error.message : 'Kh√¥ng th·ªÉ upload ·∫£nh t·ª´ URL',
    };
  }
}

